import {
  PasswordInput,
  EmailInput,
  Button,
  Input,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Link, useNavigate } from "react-router-dom";
import useForm from "../../hooks/useForm";

import MotionElement from "../../components/motion-element/motion-element";

import { userRegisterRequest } from "../../services/auth/registerApi";

import { useDispatch } from "react-redux";

const Register = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { formData, handleChange } = useForm({});

  const onChange = (e) => {
    handleChange(e);
  };

  const onSubmit = () => {
    dispatch(userRegisterRequest(formData));
    navigate("/login");
  };

  return (
    <section className="container">
      <MotionElement>
        <h2 className="text text_type_main-medium"></h2>
        <form>
          <Input
            type="text"
            placeholder=""
            name="name"
            error={false}
            errorText=""
            size="default"
            extraClass="mt-6"
            value={formData.name || ""}
            onChange={onChange}
          />
          <EmailInput
            name="email"
            isIcon={false}
            extraClass="mt-6"
            autoComplete="current-email"
            value={formData.email || ""}
            onChange={onChange}
          />
          <PasswordInput
            name="password"
            extraClass="mt-6"
            autoComplete="current-password"
            value={formData.password || ""}
            onChange={onChange}
          />
          <Button
            htmlType="button"
            type="primary"
            size="medium"
            extraClass="mt-6 mb-20"
            onClick={onSubmit}
          >
            
          </Button>
        </form>
        <div className="container__text-wrapper text text_type_main-default mb-4">
          <p className="container__text-title text"> ?</p>
          <Link className="container__text-link" to={"/login"}>
            
          </Link>
        </div>
      </MotionElement>
    </section>
  );
};

export default Register;
